* {
  font-family: "Satoshi", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  direction: ltr;
}

body {
  font-family: "Satoshi", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Inter", sans-serif;
}

ul,
ol,
li {
  list-style: none;
}

a {
  text-decoration: none;
}

.container {
  max-width: 1170px;
  margin: auto;
  position: relative;
}

.row {
  display: flex;
  justify-content: center;
}

.img-fluid {
  display: block;
  width: 100%;
  height: auto;
}

#nav .navbar {
  font-size: 18px;
  height: 60px;
  margin: 24px 0 24px 0;
  display: flex;
  justify-content: space-between;
}
#nav .navbar ul li {
  display: inline-block;
  line-height: 60px;
  margin: 0 0 0 18px;
  position: relative;
  cursor: pointer;
}
#nav .navbar ul li::after {
  content: "";
  position: absolute;
  right: 0;
  left: 0;
  bottom: 0;
  width: 0;
  height: 2px;
  background-color: rgb(54, 114, 228);
  transition: width 0.5s ease;
}
#nav .navbar ul li:hover::after {
  width: 100%;
}
#nav .navbar ul li a {
  color: rgb(70, 70, 70);
  transition: color 1.5s ease;
}
#nav .navbar ul li:hover a {
  color: rgb(54, 114, 228);
}
#nav .navbar .social-icons {
  direction: ltr;
  padding: 21px 0 0 0;
}
#nav .navbar .social-icons a {
  color: #545858;
  transition: color 1s ease;
}
#nav .navbar .social-icons a:hover {
  color: #3f3d3d;
}
#nav .navbar .social-icons a i {
  margin: 0 8px 0 0;
}

#header header {
  width: 100%;
  margin: 100px 0 0 0;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
#header header .right {
  display: flex;
  position: relative;
  flex-basis: 67%;
  max-width: 67%;
}