// Font Variable
$fonts-path: "../assets/fonts";
$font-weight-light: 300;
$font-weight-regular: 400;
$font-weight-medium: 500;
$font-weight-bold: 700;
$font-style: "normal";
$font-display: "swap";
$font-family-satoshi: "Satoshi", sans-serif;
$font-family-inter: "Inter", sans-serif;
$defult-font-size: 16px;
// variable
$nav-link: rgb(70, 70, 70);
$nav-link-hover: rgb(54, 114, 228);
$social-icons-color: #545858;
$social-icons-color-hover: #3f3d3d;
//mixin
@mixin flex($flex: flex, $position-x: center, $position-y: center) {
  display: $flex;
  justify-content: $position-x;
  align-items: $position-y;
}
@mixin margin-padding($top, $right, $bottom, $left, $margin: true) {
  @if ($margin) {
    margin: $top $right $bottom $left;
  } @else {
    padding: $top $right $bottom $left;
  }
}

// rest
@font-face {
  font-family: "$font-family-satoshi";
  src: url("#{$fonts-path}/Satoshi-Light.woff2") format("woff2"),
    url("#{$fonts-path}/Satoshi-Light.ttf") format("truetype");
  font-weight: $font-weight-light;
  font-style: $font-style;
  font-display: $font-display;
}

@font-face {
  font-family: "$font-family-satoshi";
  src: url("#{$fonts-path}/Satoshi-Regular.woff2") format("woff2"),
    url("#{$fonts-path}/Satoshi-Regular.ttf") format("truetype");
  font-weight: $font-weight-regular;
  font-style: $font-style;
  font-display: $font-display;
}

@font-face {
  font-family: "$font-family-satoshi";
  src: url("#{$fonts-path}/Satoshi-Medium.woff2") format("woff2"),
    url("#{$fonts-path}/Satoshi-Medium.ttf") format("truetype");
  font-weight: $font-weight-medium;
  font-style: $font-style;
  font-display: $font-display;
}

@font-face {
  font-family: "$font-family-satoshi";
  src: url("#{$fonts-path}/Satoshi-Bold.woff2") format("woff2"),
    url("#{$fonts-path}/Satoshi-Bold.ttf") format("truetype");
  font-weight: $font-weight-bold;
  font-style: $font-style;
  font-display: $font-display;
}
/* ================================
   INTER FONT
   For Paragraphs & Details
================================= */

/* Light (300) */
@font-face {
  font-family: "$font-family-inter";
  src: url("#{$fonts-path}/Inter_18pt-Light.ttf") format("truetype"),
    url("#{$fonts-path}/Inter_24pt-Light.ttf") format("truetype"),
    url("#{$fonts-path}/Inter_28pt-Light.ttf") format("truetype");
  font-weight: $font-weight-light;
  font-style: $font-style;
  font-display: $font-display;
}

/* Regular (400) */
@font-face {
  font-family: "$font-family-inter";
  src: url("#{$fonts-path}/Inter_18pt-Regular.ttf") format("truetype"),
    url("#{$fonts-path}/Inter_24pt-Regular.ttf") format("truetype"),
    url("#{$fonts-path}/Inter_28pt-Regular.ttf") format("truetype");
  font-weight: $font-weight-regular;
  font-style: $font-style;
  font-display: $font-display;
}

/* Medium (500) */
@font-face {
  font-family: "$font-family-inter";
  src: url("#{$fonts-path}/Inter_18pt-Medium.ttf") format("truetype"),
    url("#{$fonts-path}/Inter_24pt-Medium.ttf") format("truetype"),
    url("#{$fonts-path}/Inter_28pt-Medium.ttf") format("truetype");
  font-weight: $font-weight-medium;
  font-style: $font-style;
  font-display: $font-display;
}

/* SemiBold (600) */
@font-face {
  font-family: "$font-family-inter";
  src: url("#{$fonts-path}/Inter_28pt-SemiBold.ttf") format("truetype");
  font-weight: 600;
  font-style: $font-style;
  font-display: $font-display;
}
// rest
* {
  font-family: $font-family-satoshi;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  direction: ltr;
}
body {
  font-family: $font-family-satoshi;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: $font-family-inter;
}
ul,
ol,
li {
  list-style: none;
}
a {
  text-decoration: none;
}
.container {
  max-width: 1170px;
  margin: auto;
  position: relative;
}
.row {
  @include flex(flex, center, null);
}
.img-fluid {
  display: block;
  width: 100%;
  height: auto;
}
// menu style
#nav {
  .navbar {
    font-size: $defult-font-size + 2px;
    height: 60px;
    @include margin-padding(24px, 0, 24px, 0);
    @include flex(flex, space-between, null);
    ul {
      li {
        display: inline-block;
        line-height: 60px;
        @include margin-padding(0, 0, 0, 18px);
        position: relative;
        cursor: pointer;
        &::after {
          content: "";
          position: absolute;
          // top: 0;
          right: 0;
          left: 0;
          bottom: 0;
          width: 0;
          height: 2px;
          background-color: $nav-link-hover;
          transition: width 0.5s ease;
        }
        &:hover::after {
          width: 100%;
        }
        a {
          color: $nav-link;
          transition: color 1.5s ease;
          // &:hover {
          //   color: rgb(54, 114, 228);
          // }
        }
        &:hover a {
          color: $nav-link-hover;
        }
      }
    }
    .social-icons {
      direction: ltr;
      @include margin-padding(21px, 0, 0, 0, false);
      a {
        color: $social-icons-color;
        transition: color 1s ease;
        &:hover {
          color: $social-icons-color-hover;
        }
        i {
          @include margin-padding(0, 8px, 0, 0);
        }
      }
    }
  }
}
#header{
  header{
    width: 100%;
    @include margin-padding(100px, 0, 0, 0);
    @include flex(flex, space-between, flex-start);
    .right{
      display: flex;
      position: relative;
      flex-basis: 67%;
      max-width: 67%;
    }
  }
}
